import React from 'react'
import posed from 'react-pose'
import { tween } from 'popmotion'
import { interpolate } from 'flubber'
import styled from 'styled-components'

const StyledBody = styled.div`
  height: 40vh;
  padding: 100px 0;
  flex-flow: column;
  justify-content: stretch;
  align-items: center;
  display: flex;
  background: #f4f3fa;

  /* #root {
    color: #fff;
    font-size: 32px;
    font-family: PT Sans, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 200;
    text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
  } */

  button {
    display: flex;
    border: none;
    margin-bottom: 20px;
    width: auto;
    overflow: visible;
    background: transparent;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    -webkit-appearance: none;
    background: transparent;
    outline: none;
    color: white;
    padding: 15px 20px;
    cursor: pointer;
    color: rgb(70, 200, 120);
    font-size: 2rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
  }
`

const paths = {
  plane:
    'M229.08,148.23v40.43l-40.42-40.43H107.8a27,27,0,0,1-26.95-26.95V27A27,27,0,0,1,107.8,0H242.55A27,27,0,0,1,269.5,27v94.33a27,27,0,0,1-26.95,26.95Zm-40.42,27v26.95a27,27,0,0,1-27,26.95H80.85L40.42,269.5V229.08H27A27,27,0,0,1,0,202.13V107.8A27,27,0,0,1,27,80.85h27v40.43a53.9,53.9,0,0,0,53.9,53.9Z',
  heart:
    'M313.31,0a34.81,34.81,0,0,1,34.81,34.81V243.69a34.81,34.81,0,0,1-34.81,34.81H34.81A34.81,34.81,0,0,1,0,243.69V34.81A34.91,34.91,0,0,1,34.81,0ZM237.25,158.4l110.88,85.29V208.88L259,141l89.12-71.37V34.81L174.06,174.06,0,34.81V69.63L89.12,141,0,208.88v34.81L110.88,158.4l63.18,50.48Z',
  bell:
    'M41.4,110.19A82.8,82.8,0,0,1,97,32a27.6,27.6,0,1,1,54.51,0A82.8,82.8,0,0,1,207,110.19V193l41.4,27.6v13.8H0v-13.8L41.4,193Zm110.39,138a27.6,27.6,0,1,1-55.2,0Z',
}
const text = {
  first: 'Refreshingly Simple Text Message Appointment Reminders',
  second: 'Calendar',
  third: 'Perfect Collection of business tools to help you grow',
}
const textIds = Object.keys(text)
const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.9 },
})

class IconMorph extends React.Component {
  state = {
    pathIndex: 0,
    buttonText: text.first,
  }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg
          width="120"
          height="120"
          viewBox="0 0 375.13 307.15"
          fill="rgb(70, 200, 120)"
        >
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.gotoNext}>{this.state.buttonText}</NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
